.panel > h3 {
  margin-top: 7%;
  margin-left: 2vw;
  color: #F6F6F6;
  font-weight: normal;
  font-size: 1em;
  text-align: left;
}

ul, li {
  list-style: none;
}

.nav-link {
  font-size: 0.8em;
  color: #7F2EFF;
  margin-top: 3.5%;
  margin-left: -4px;
  text-decoration: none;
}

.lineas {
  height: 7px; 
  width: 1vwpx;
}

.infoKey {
  display: flex;
  flex-direction: row;
  width: 100%;
  color: white;
}
.dataKey {
  display: flex;
  width: 300px;
  color: white;
}

.mapClass {
  display: flex;
  flex-direction: column;
  left: 50px;
  margin-right: 10vw;
  width: 70vw;
  height: 100vh;
}

#tooltip {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 11px;
  position: absolute;
  padding: 4px;
  margin: 8px;
  background: rgba(1, 1, 1, 0.5);
  color: #fff;
  max-width: 150px;
  font-size: 10px;
  z-index: 9;
  pointer-events: none;
}

canvas {
  position: static !important;
  left: 50px;
}
#deckgl-wrapper {
  left: 50px;
}

.mapContainer {
  height: 70%;
  color: black;
  left: 50px;
}

.button3d2d {
  display: flex;
  flex-direction: row;
  background-color: #ff312e;
  height: 30px;
  margin: 0 auto;
  width: 100%;
  top: 0;
}

.activeButtonStyle {
  display: flex;
  flex-direction: row;
  width: 35vw;
  height: 30px;
  top: 0px;
  background-color: #ff312e;
  text-align: center;
  margin: 0 auto;
}

.inactiveButtonStyle {
  display: flex;
  flex-direction: row;
  width: 35vw;
  height: 30px;
  top: 0px;
  background-color: #2c1364;
  text-align: center;
  margin: 0 auto;
}

.buttonStyle .inactiveButtonStyle span {
  float: center;
  position: relative;
  bottom: 18px;
  text-align: center;
  width: 100%;
}

.buttonStyle .activeButtonStyle span {
  float: center;
  position: relative;
  bottom: 18px;
  text-align: center;
  width: 100%;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.header {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  overflow: hidden;
  background-color: #161022;
  color: white;
  padding: 0px 0px;
  margin: 0 0 0 0;
  height: 90px;
}

.header a {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  float: left;
  color: white;
  text-align: center;
  padding: 0px 15px;
  text-decoration: none;
  font-size: 14px;
  line-height: 25px;
  border-radius: 4px;
}

.header a.logo {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1.5em;
  width: 180px;
  text-align: left;
  vertical-align: text-top;
  top: 0;
  margin-top: -12px;
  padding: 0 0 0 0;
  color: white;
}

.header a:hover {
  background-color: #ddd;
  color: black;
}

.header h5 {
  margin: -1px;
}

.header a.active {
  /* background-color: #2c1364;
  color: white; */
}

.header-right {
  float: right;
}

@media screen and (max-width: 500px) {
  .header a {
    float: none;
    display: block;
    text-align: left;
  }
  .header-right {
    float: none;
  }
}
